<template>
  <tablepage
    :showData="showData"
    :total="total"
    @pageChange="getPageData"
  ></tablepage>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "locationRank",
  data() {
    return {
      test: this.$store.state.location.total,
    };
  },
  computed: {
    ...mapState({
      showData: (state) => state.location.pageData,
      total: (state) => state.location.total,
    }),
  },
  methods: {
    getPageData(page) {
      this.$store.dispatch("location/sendLocationRequest", {
        page: page,
      });
    },
  },
  mounted() {
    this.getPageData(1);
  },
};
</script>

